html, body, #root {
  margin: 0;
	height:100%;
  width: 100%;
  }
  
  /*整體區塊*/
  .MainBlock {
	  height:100%;
    width: 100%;
  }
  
/*上方區塊*/
  #topBlock{
    display: flex;
    background: #ff7380;
    height:8%;
	  width:100%;
    text-align: center;
    justify-content: space-between;
    align-items: center;
  }

  
  /*上方區塊之中間字體樣式*/
  .topText{
    color: #ffffff;         
    font-size: 55px;       
    font-family: SignPainter-HouseScript;
  }
  
  /*上方區塊*/
  #cotentBlock{
    display:flex;
    height:92%;
	  width:100%;
  }
